import React, { Component } from 'react';

import { connect } from 'react-redux';
import { addTodo, toggleCompleteTodo, removeTodo, editTodo, setVisibilityFilter, VisibilityFilters } from './../redux/actions';

import AddTodo from '../components/AddTodo';
import TodoList from '../components/TodoList';
import Footer from '../components/Footer';


@connect((state) => {
  return {
    visibleTodos: selectTodos(state.todos, state.visibilityFilter),
    visibilityFilter: state.visibilityFilter
  };
})
export default class Home extends Component {
  render() {
    // Injected by connect() call:
    const { dispatch, visibleTodos, visibilityFilter } = this.props;
    return (
        <div>
          <AddTodo
              onAddClick={text =>
            dispatch(addTodo(text))
          } />
          <TodoList
              todos={visibleTodos}
              onTodoClick={index => dispatch(toggleCompleteTodo(index))}
              onTodoRemove={index => dispatch(removeTodo(index))}
              onTodoEdit={(index, text) => dispatch(editTodo(index, text))}
          />
          <Footer
              filter={visibilityFilter}
              onFilterChange={nextFilter => dispatch(setVisibilityFilter(nextFilter))
          } />
        </div>
    );
  }
}

function selectTodos(todos, filter) {
  switch (filter) {
    case VisibilityFilters.SHOW_ALL:
      return todos;
    case VisibilityFilters.SHOW_COMPLETED:
      return todos.filter(todo => todo.completed);
    case VisibilityFilters.SHOW_ACTIVE:
      return todos.filter(todo => !todo.completed);
  }
}


